.home .banners {
  padding: 20px 30px;
  width: 100%;
}

.home .banners .container {
  max-width: 100%;
  display: flex;
}

.home .banners .container .left_box {
  width: 24%;
}

.home .banners .container .left_box .box {
  margin-top: 10px;
}

.home .banners .container .right_box {
  width: 75%;
}

.home .banners .container .right_box .top {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.home .banners .container .bottom {
  max-width: 100%;
  margin-top: 25px;
}

.home .banners .container .right_box .bottom img {
  width: 100%;
  object-fit: cover;
}

.home .product_type {
  padding: 20px 30px;
  width: 100%;
}

.home .product_type .container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; /* allow wrapping when screen is smaller */
  gap: 20px; /* spacing between product boxes */
  height: auto; /* allow container to grow */
}

.home .product_type .container .box {
  flex: 1 1 30%; /* each box takes ~30% of row, flexible */
  max-width: 360px; /* cap width so they don’t stretch too much */
  border: 3px solid #f6f7f8;
  overflow: hidden;
  height: auto; /* don’t force a fixed height */
  padding-bottom: 20px; /* space for last item */
}

.home .product_type .container .box .header {
  width: 100%;
  height: 50px;
  background: #f6f6f6;
  padding: 10px 10px;
}

.home .product_type .container .box .header h2 {
  color: #232323;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 600;
}

.home .product_type .container .box .productbox {
  width: 100%;
  height: auto; /*120px;*/
  padding: 10px; /*10px 10px;*/
  border: 1px solid #f6f7f8;
  display: flex;
  align-item: center;
}

.home .product_type .container .box .productbox .img-box {
  width: 150px;
  height: 150px; /* square container */
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden; /* prevents overflow but doesn’t crop */
}

.home .product_type .container .box .productbox .img-box img {
  max-width: 100%;
  max-height: 100%; /*150px;*/
  object-fit: contain;
}

.home .product_type .container .box .productbox .detail {
  margin-left: 30px;
}

.home .product_type .container .box .productbox .detail h3 {
  font-size: 14px;
  color: #8a8a8a;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.home .product_type .container .box .productbox .detail p {
  margin-top: 5px;
  color: #cd1e76;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 16px;
}

.home .product_type .container .box .productbox .detail .icon {
  margin-top: 10px;
  display: flex;
}

.home .product_type .container .box .productbox .detail .icon button {
  background: none;
  outline: none;
  border: none;
  padding: 10px 10px;
  border: 2px solid #f6f7f8;
  margin-left: 10px;
  border-radius: 50%;
  color: #8a8a8a;
  cursor: pointer;
  transition: 0.5s;
}

.home .product_type .container .box .productbox .detail .icon button:hover {
  background: #f6f6f6;
  color: #232323;
}
